import Vue from 'vue'
import Router from 'vue-router'

import Main from './views/Main'
import ChartView from './views/ChartView'

Vue.use(Router)

export default new Router({
    mode: 'history',
    base: process.env.BASE_URL,
    routes: [
        {
            path: '/',
            name: 'home',
            component: Main
        },
        {
            path: '/Chart',
            name: 'ChartView',
            component: ChartView
        }
    ]
})